export const Teagan = { "name": "Teagan Lynd", "classData": { "name": "Bard", "misc": {}, "url": "https://roll20.net/compendium/dnd5e/", "start_prof": { "wpn": ["Simple Weapons", "Hand Crossbows", "Longswords", "Rapiers", "Shortswords"], "tool": ["Disguise Kit", "Guitar", "Thieves Tools", "Singing"], "armr": ["Light"] }, "spcMod": "chr", "save_throws": ["dex", "chr"], "hitdie": "d8" }, "lvl": 7, "statsData": { "save_throws": ["dex", "chr"], "ability": { "str": "8", "dex": "14", "cnst": "16", "int": "8", "wis": "10", "chr": "19" }, "inspiration": "", "misc_prof": { "lang": ["Common", "Elvish"], "wpn": ["Simple Weapons", "Hand Crossbows", "Longswords", "Rapiers", "Shortswords"], "tool": ["Disguise Kit", "Guitar", "Thieves Tools", "Singing", "Wind Instruments"], "armr": ["Light"] }, "misc_notes": "=====================\n     🎸 Teagan Lynd 🎸\n=====================\nBackground: Entertainer\nRace: Human (variant)\n\nBardic Inspo (d8; 4): \n\nSpell Slots:\nLevel 0: 3 known\nLevel 1 (4): \nLevel 2 (3): \nLevel 3 (3): \nLevel 4 (1): \n\ndegrade -1\n\ntempeny tower\n\ndrunken nuka cola\n\nInventory:\n- Electric Guitar 🎸\n- Desert Eagle (dmg: 2d8 rng: 30/120 mag: 8)\n- Mag: (8/8)\n- Bullets: 75\n- Leather Jacket (11 + Dex modifier) + 2 AC armour padding\n- Red tinted circular glasses\n- Disguise Kit\n- Supply: 29\n- Pack of Cigarettes\n- 1x Greater Stimpack (4d4+4)\n- Combat Knife (Light, Thrown, Finesse, rng: 20/40, dmg: d8)\n- Slave Collar\n\nBy Popular Demand\nYou can always find a place to perform, usually in an inn or tavern but possibly with a circus, at a theater, or even in a noble's court. At such a place, you receive free lodging and food of a modest or comfortable standard (depending on the quality of the establishment), as long as you perform each night. In addition, your performance makes you something of a local figure. When strangers recognize you in a town where you have performed, they typically take a liking to you.\n\nSettling a Score:\n\"Ironsights\" is the guy who sold you out --DEAD--\n\"Grik\" is the \"Raider Boss\"\n\nSkill Expert (Feat)\nYou have honed your proficiency with particular skills, granting you the following benefits:\n\nIncrease one ability score of your choice by 1, to a maximum of 20.\nYou gain proficiency in one skill of your choice.\nChoose one skill in which you have proficiency. You gain expertise with that skill, which means your proficiency bonus is doubled for any ability check you make with it. The skill you choose must be one that isn't already benefiting from a feature, such as Expertise, that doubles your proficiency bonus.\n\nRitual Casting\nYou can cast any bard spell you know as a ritual if that spell has the ritual tag.\n\nSpellcasting Focus\nYou can use a musical instrument (found in chapter 5) as a spellcasting focus for your bard spells.\n\nBardic Inspiration\nYou can inspire others through stirring words or music. To do so, you use a bonus action on your turn to choose one creature other than yourself within 60 feet of you who can hear you. That creature gains one Bardic Inspiration die, a d6.\n\nOnce within the next 10 minutes, the creature can roll the die and add the number rolled to one ability check, attack roll, or saving throw it makes. The creature can wait until after it rolls the d20 before deciding to use the Bardic Inspiration die, but must decide before the DM says whether the roll succeeds or fails. Once the Bardic Inspiration die is rolled, it is lost. A creature can have only one Bardic Inspiration die at a time.\n\nYou can use this feature a number of times equal to your Charisma modifier (a minimum of once). You regain any expended uses when you finish a long rest.\n\nYour Bardic Inspiration die changes when you reach certain levels in this class. The die becomes a d8 at 5th level, a d10 at 10th level, and a d12 at 15th level.\n\nMagical Inspiration (Optional)\nAt 2nd level, if a creature has a Bardic Inspiration die from you and casts a spell that restores hit points or deals damage, the creature can roll that die and choose a target affected by the spell. Add the number rolled as a bonus to the hit points regained or the damage dealt. The Bardic Inspiration die is then lost.\n\nJack of All Trades\nStarting at 2nd level, you can add half your proficiency bonus, rounded down, to any ability check you make that doesn't already include your proficiency bonus.\n\nSong of Rest\nBeginning at 2nd level, you can use soothing music or oration to help revitalize your wounded allies during a short rest. If you or any friendly creatures who can hear your performance regain hit points at the end of the short rest by spending one or more Hit Dice, each of those creatures regains an extra 1d6 hit points.\n\nThe extra Hit Points increase when you reach certain levels in this class: to 1d8 at 9th level, to 1d10 at 13th level, and to 1d12 at 17th level.\n\nSilver Tongue\nStarting at 3rd level, you are a master at saying the right thing at the right time. When you make a Charisma (Persuasion) or Charisma (Deception) check, you can treat a d20 roll of 9 or lower as a 10.\n\nUnsettling Words\nAlso at 3rd level, you can spin words laced with magic that unsettle a creature and cause it to doubt itself. As a bonus action, you can expend one use of your Bardic Inspiration and choose one creature you can see within 60 feet of you. Roll the Bardic Inspiration die. The creature must subtract the number rolled from the next saving throw it makes before the start of your next turn.\n\nExpertise\nAt 3rd level, choose two of your skill proficiencies. Your proficiency bonus is doubled for any ability check you make that uses either of the chosen proficiencies.\n\nAt 10th level, you can choose another two skill proficiencies to gain this benefit.\n\nCountercharm\nAt 6th level, you gain the ability to use musical notes or words of power to disrupt mind-influencing effects. As an action, you can start a performance that lasts until the end of your next turn. During that time, you and any friendly creatures within 30 feet of you have advantage on saving throws against being frightened or charmed. A creature must be able to hear you to gain this benefit. The performance ends early if you are incapacitated or silenced or if you voluntarily end it (no action required).\n\nUnfailing Inspiration\nAt 6th level, your inspiring words are so persuasive that others feel driven to succeed. When a creature adds one of your Bardic Inspiration dice to its ability check, attack roll, or saving throw and the roll fails, the creature can keep the Bardic Inspiration die.\n\nUniversal Speech\nAlso at 6th level, you have gained the ability to make your speech intelligible to any creature. As an action, choose one or more creatures within 60 feet of you, up to a number equal to your Charisma modifier (minimum of one creature). The chosen creatures can magically understand you, regardless of the language you speak, for 1 hour.\n\nOnce you use this feature, you can't use it again until you finish a long rest, unless you expend a spell slot to use it again.\n", "skill_modifiers": { "acrobatics": { "name": "Acrobatics", "raw": "dex", "expert": false, "proficent": true }, "animal_handling": { "name": "Animal Handling", "raw": "wis", "expert": false, "proficent": false }, "arcana": { "name": "Arcana", "raw": "int", "expert": false, "proficent": false }, "athletics": { "name": "Athletics", "raw": "str", "expert": false, "proficent": false }, "deception": { "name": "Deception", "raw": "chr", "expert": true, "proficent": true }, "history": { "name": "History", "raw": "int", "expert": false, "proficent": false }, "insight": { "name": "Insight", "raw": "wis", "expert": false, "proficent": true }, "intimidation": { "name": "Intimidation", "raw": "chr", "expert": true, "proficent": true }, "investigation": { "name": "Investigation", "raw": "int", "expert": false, "proficent": false }, "medicine": { "name": "Medicine", "raw": "wis", "expert": false, "proficent": false }, "nature": { "name": "Nature", "raw": "int", "expert": false, "proficent": false }, "perception": { "name": "Perception", "raw": "wis", "expert": false, "proficent": false }, "performance": { "name": "Performance", "raw": "chr", "expert": false, "proficent": true }, "persuasion": { "name": "Persuasion", "raw": "chr", "expert": false, "proficent": true }, "religon": { "name": "Religon", "raw": "int", "expert": false, "proficent": false }, "sleight_of_hand": { "name": "Sleight of Hand", "raw": "dex", "expert": true, "proficent": true }, "stealth": { "name": "Stealth", "raw": "dex", "expert": false, "proficent": false }, "survival": { "name": "Survival", "raw": "wis", "expert": false, "proficent": false } } }, "healthData": { "maxHP": 63, "currentHP": 63, "defaultAC": "15", "currentAC": "15", "hitdie": "7d8" }, "exp": 23720, "magicData": { "spells": { "Vicious Mockery": { "name": "Vicious Mockery", "level": "Cantrip", "ctime": "1 Action", "ritual": "no", "concentration": "no", "school": "Enchantment", "description": "You unleash a string of insults laced with subtle enchantments at a creature you can see within range. If the target can hear you (though it need not understand you), it must succeed on a Wisdom saving throw or take 1d4 psychic damage and have disadvantage on the next attack roll it makes before the end of its next turn.\n\nThis spell's damage increases by 1d4 when you reach 5th level (2d4), 11th level (3d4), and 17th level (4d4).", "components": "V", "duration": "Instantaneous", "range": "60 ft", "roll": "1d4", "url": "https://www.dndbeyond.com/spells/vicious-mockery" }, "Minor Illusion": { "name": "Minor Illusion", "level": "Cantrip", "ctime": "1 Action", "ritual": "no", "concentration": "no", "school": "Illusion", "description": "You create a sound or an image of an object within range that lasts for the duration. The illusion also ends if you dismiss it as an action or cast this spell again.\n\nIf you create a sound, its volume can range from a whisper to a scream. It can be your voice, someone else's voice, a lion's roar, a beating of drums, or any other sound you choose. The sound continues unabated throughout the duration, or you can make discrete sounds at different times before the spell ends.\n\nIf you create an image of an object--such as a chair, muddy footprints, or a small chest--it must be no larger than a 5-foot cube. The image can't create sound, light, smell, or any other sensory effect. Physical interaction with the image reveals it to be an illusion, because things can pass through it.\n\nIf a creature uses its action to examine the sound or image, the creature can determine that it is an illusion with a successful Intelligence (Investigation) check against your spell save DC. If a creature discerns the illusion for what it is, the illusion becomes faint to the creature.", "components": "S, M *", "duration": "1 Minute", "range": "30 ft (5 ft *)", "roll": null, "url": "https://www.dndbeyond.com/spells/minor-illusion" }, "Prestidigitation": { "name": "Prestidigitation", "level": "Cantrip", "ctime": "1 Action", "ritual": "no", "concentration": "no", "school": "Transmutation", "description": "This spell is a minor magical trick that novice spellcasters use for practice. You create one of the following magical effects within range:\n\n- You create an instantaneous, harmless sensory effect, such as a shower of sparks, a puff of wind, faint musical notes, or an odd odor.\n\n- You instantaneously light or snuff out a candle, a torch, or a small campfire.\n\n- You instantaneously clean or soil an object no larger than 1 cubic foot.\n\n- You chill, warm, or flavor up to 1 cubic foot of nonliving material for 1 hour.\n\n- You make a color, a small mark, or a symbol appear on an object or a surface for 1 hour.\n\n- You create a nonmagical trinket or an illusory image that can fit in your hand and that lasts until the end of your next turn.\n\nIf you cast this spell multiple times, you can have up to three of its non-instantaneous effects active at a time, and you can dismiss such an effect as an action.", "components": "V, S", "duration": "1 Hour", "range": "10 ft", "roll": "0d4", "url": "https://dnd5e.fandom.com/wiki/Prestidigitation" }, "Command": { "name": "Command", "level": "1st", "ctime": "1 Action", "ritual": "no", "concentration": "no", "school": "Enchantment", "description": "You speak a one-word command to a creature you can see within range. The target must succeed on a Wisdom saving throw or follow the command on its next turn. The spell has no effect if the target is undead, if it doesn't understand your language, or if your command is directly harmful to it.\n\nSome typical commands and their effects follow. You might issue a command other than one described here. If you do so, the GM determines how the target behaves. If the target can't follow your command, the spell ends.\n\nApproach. The target moves toward you by the shortest and most direct route, ending its turn if it moves within 5 feet of you.\n\nDrop. The target drops whatever it is holding and then ends its turn.\n\nFlee. The target spends its turn moving away from you by the fastest available means.\n\nGrovel. The target falls prone and then ends its turn.\n\nHalt. The target doesn't move and takes no actions. A flying creature stays aloft, provided that it is able to do so. If it must move to stay aloft, it flies the minimum distance needed to remain in the air.\n\nAt Higher Levels. When you cast this spell using a spell slot of 2nd level or higher, you can affect one additional creature for each slot level above 1st. The creatures must be within 30 feet of each other when you target them.", "components": "V", "duration": "1 Round", "range": "60 ft", "roll": null, "url": "https://www.dndbeyond.com/spells/command" }, "Heroism": { "name": "Heroism", "level": "1st", "ctime": "1 Action", "ritual": "no", "concentration": "no", "school": "Enchantment", "description": "A willing creature you touch is imbued with bravery. Until the spell ends, the creature is immune to being frightened and gains temporary hit points equal to your spellcasting ability modifier at the start of each of its turns. When the spell ends, the target loses any remaining temporary hit points from this spell.\n\nAt Higher Levels. When you cast this spell using a spell slot of 2nd level or higher, you can target one additional creature for each slot level above 1st.", "components": "V, S", "duration": "Concentration 1 Minute", "range": "Touch", "roll": null, "url": "https://www.dndbeyond.com/spells/heroism" }, "Silvery Barbs": { "name": "Silvery Barbs", "level": "1st", "ctime": "1 reaction, which you take when a creature you can see within 60 feet of yourself succeeds on an attack roll, an ability check, or a saving throw", "ritual": "no", "concentration": "no", "school": "Enchantment", "description": "You magically distract the triggering creature and turn its momentary uncertainty into encouragement for another creature. The triggering creature must reroll the d20 and use the lower roll.\n\nYou can then choose a different creature you can see within range (you can choose yourself). The chosen creature has advantage on the next attack roll, ability check, or saving throw it makes within 1 minute. A creature can be empowered by only one use of this spell at a time.", "components": "V", "duration": "Instantaneous", "range": "60 feet", "roll": "0d4", "url": "https://dnd5e.fandom.com/wiki/Silvery_Barbs" }, "Unearthly Chorus": { "name": "Unearthly Chorus", "level": "1st", "ctime": "1 Action", "ritual": "no", "concentration": "no", "school": "Illusion", "description": "Music of a style you choose fills the air around you in a 30-foot radius. The music spreads around corners and can be heard from up to 100 feet away. The music moves with you, centered on you for the duration.\n\nUntil the spell ends, you make Charisma (Performance) checks with advantage. In addition, you can use a bonus action on each of your turns to beguile one creature you choose within 30 feet of you that can see you and hear the music. The creature must make a Charisma saving throw. If you or your companions are attacking it, the creature automatically succeeds on the saving throw. On a failure, the creature becomes friendly to you for as long as it can hear the music and for 1 hour thereafter. You make Charisma (Deception) checks and Charisma (Persuasion) checks against creatures made friendly by this spell with advantage.", "components": "V", "duration": "Concentration, up to 10 minutes", "range": "Self (30 ft radius)", "roll": "0d4", "url": "https://dnd5e.fandom.com/wiki/Unearthly_Chorus" }, "Suggestion": { "name": "Suggestion", "level": "2nd", "ctime": "1 Action", "ritual": "no", "concentration": "no", "school": "Enchantment", "description": "You suggest a course of activity (limited to a sentence or two) and magically influence a creature you can see within range that can hear and understand you. Creatures that can't be charmed are immune to this effect. The suggestion must be worded in such a manner as to make the course of action sound reasonable. Asking the creature to stab itself, throw itself onto a spear, immolate itself, or do some other obviously harmful act ends the spell.\n\nThe target must make a Wisdom saving throw. On a failed save, it pursues the course of action you described to the best of its ability. The suggested course of action can continue for the entire duration. If the suggested activity can be completed in a shorter time, the spell ends when the subject finishes what it was asked to do.\n\nYou can also specify conditions that will trigger a special activity during the duration. For example, you might suggest that a knight give her warhorse to the first beggar she meets. If the condition isn't met before the spell expires, the activity isn't performed.\n\nIf you or any of your companions damage the target, the spell ends.", "components": "V, M *", "duration": "Concentration 8 Hours", "range": "30 ft", "roll": null, "url": "https://www.dndbeyond.com/spells/suggestion" }, "Heat Metal": { "name": "Heat Metal", "level": "2nd", "ctime": "1 Action", "ritual": "no", "concentration": "no", "school": "Transmutation", "description": "Choose a manufactured metal object, such as a metal weapon or a suit of heavy or medium metal armor, that you can see within range. You cause the object to glow red-hot. Any creature in physical contact with the object takes 2d8 fire damage when you cast the spell. Until the spell ends, you can use a bonus action on each of your subsequent turns to cause this damage again. If a creature is holding or wearing the object and takes the damage from it, the creature must succeed on a Constitution saving throw or drop the object if it can. If it doesn't drop the object, it has disadvantage on attack rolls and ability checks until the start of your next turn.\n\nAt Higher Levels. When you cast this spell using a spell slot of 3rd level or higher, the damage increases by 1d8 for each slot level above 2nd.", "components": "V, S, M *", "duration": "Concentration 1 Minute", "range": "60 ft", "roll": "2d8", "url": "https://www.dndbeyond.com/spells/heat-metal" }, "Hold Person": { "name": "Hold Person", "level": "2nd", "ctime": "1 Action", "ritual": "no", "concentration": "no", "school": "Enchantment", "description": "Choose a humanoid that you can see within range. The target must succeed on a Wisdom saving throw or be paralyzed for the duration. At the end of each of its turns, the target can make another Wisdom saving throw. On a success, the spell ends on the target.\n\nAt Higher Levels. When you cast this spell using a spell slot of 3rd level or higher, you can target one additional humanoid for each slot level above 2nd. The humanoids must be within 30 feet of each other when you target them.", "components": "V, S, M *", "duration": "Concentration 1 Minute", "range": "60 ft", "roll": null, "url": "https://www.dndbeyond.com/spells/hold-person" }, "Mass Healing Word": { "name": "Mass Healing Word", "level": "3rd", "ctime": "1 Bonus Action", "ritual": "no", "concentration": "no", "school": "Evocation", "description": "As you call out words of restoration, up to six creatures of your choice that you can see within range regain hit points equal to 1d4 + your spellcasting ability modifier. This spell has no effect on undead or constructs.\n\nAt Higher Levels. When you cast this spell using a spell slot of 4th level or higher, the healing increases by 1d4 for each slot level above 3rd.", "components": "V", "duration": "Instantaneous", "range": "60 ft", "roll": "1d4", "url": "https://www.dndbeyond.com/spells/mass-healing-word" }, "Tiny Hut": { "name": "Tiny Hut", "level": "3rd", "ctime": "1 Minute", "ritual": "no", "concentration": "no", "school": "Evocation", "description": "A 10-foot-radius immobile dome of force springs into existence around and above you and remains stationary for the duration. The spell ends if you leave its area.\n\nNine creatures of Medium size or smaller can fit inside the dome with you. The spell fails if its area includes a larger creature or more than nine creatures. Creatures and objects within the dome when you cast this spell can move through it freely. All other creatures and objects are barred from passing through it. Spells and other magical effects can't extend through the dome or be cast through it. The atmosphere inside the space is comfortable and dry, regardless of the weather outside.\n\nUntil the spell ends, you can command the interior to become dimly lit or dark. The dome is opaque from the outside, of any color you choose, but it is transparent from the inside.", "components": "V, S, M *", "duration": "8 Hours", "range": "Self (10 ft)", "roll": null, "url": "https://www.dndbeyond.com/spells/tiny-hut" }, "Dimension Door": { "name": "Dimension Door", "level": "4th", "ctime": "1 Action", "ritual": "no", "concentration": "no", "school": "Conjuration", "description": "You teleport yourself from your current location to any other spot within range. You arrive at exactly the spot desired. It can be a place you can see, one you can visualize, or one you can describe by stating distance and direction, such as \"200 feet straight downward\" or \"upward to the northwest at a 45- degree angle, 300 feet.\n\n\"You can bring along objects as long as their weight doesn't exceed what you can carry. You can also bring one willing creature of your size or smaller who is carrying gear up to its carrying capacity. The creature must be within 5 feet of you when you cast this spell.\n\nIf you would arrive in a place already occupied by an object or a creature, you and any creature traveling with you each take 4d6 force damage, and the spell fails to teleport you.", "components": "V", "duration": "Instantaneous", "range": "500 ft", "roll": "4d6", "url": "https://www.dndbeyond.com/spells/dimension-door" } }, "spcMod": "chr", "preparedSpells": [] }, "invData": { "gold": 271, "backpack": {} }, "renderData": { "avatar": "https://i.pinimg.com/564x/8f/3b/87/8f3b8727a78a59fe69ec11030b16202d.jpg" } }